export function anchorsSmoothScrolling() {
	document.addEventListener('DOMContentLoaded', function () {
		const anchorLinks = document.querySelectorAll('.anchor-link');
		anchorLinks.forEach(link => {
			link.addEventListener('click', function (e) {
				e.preventDefault();
				const targetId = this.getAttribute('href').substring(1);
				const targetElement = document.getElementById(targetId);

				// ÐÐ¿ÑÐµÐ´ÐµÐ»ÑÐµÐ¼ Ð¾ÑÑÑÑÐ¿ Ð² Ð·Ð°Ð²Ð¸ÑÐ¸Ð¼Ð¾ÑÑÐ¸ Ð¾Ñ ÑÐ¸ÑÐ¸Ð½Ñ ÑÐºÑÐ°Ð½Ð°
				const screenWidth = window.innerWidth;
				let offset = 80; // ÐÐ¾ ÑÐ¼Ð¾Ð»ÑÐ°Ð½Ð¸Ñ 150px

				// ÐÑÐ»Ð¸ ÑÐ¸ÑÐ¸Ð½Ð° ÑÐºÑÐ°Ð½Ð° 768px Ð¸ Ð¼ÐµÐ½ÑÑÐµ, Ð¸ÑÐ¿Ð¾Ð»ÑÐ·ÑÐµÐ¼ Ð¾ÑÑÑÑÐ¿ 50px
				if (screenWidth <= 1024) {
					offset = 64;
				}

				const targetPosition = targetElement.getBoundingClientRect().top + window.scrollY;
				const offsetPosition = targetPosition - offset;

				// ÐÑÐ¾Ð²ÐµÑÑÐµÐ¼ Ð½Ð°Ð»Ð¸ÑÐ¸Ðµ Ð¾ÑÐºÑÑÑÐ¾Ð³Ð¾ Ð±Ð¾ÐºÐ¾Ð²Ð¾Ð³Ð¾ Ð¼ÐµÐ½Ñ
				const sidebarMenu = e.target.closest('.sidebar-menu');
				if (sidebarMenu && sidebarMenu.classList.contains('_open-menu')) {
					const buttonItems = document.querySelector('.burger-button');
					buttonItems.classList.remove('_open-menu');
					document.body.classList.remove('no-scroll');

					// ÐÑÐ¿Ð¾Ð»ÑÐ·ÑÐµÐ¼ Ð²ÑÐ½ÐµÑÐµÐ½Ð½ÑÑ ÑÑÐ½ÐºÑÐ¸Ñ Ð´Ð»Ñ ÑÐ¿ÑÐ°Ð²Ð»ÐµÐ½Ð¸Ñ Ð¼ÐµÐ½Ñ
					// toggleSidebarMenu(sidebarMenu);
				}

				window.scrollTo({
					top: offsetPosition,
					behavior: 'smooth'
				});
			});
		});
	});
}