import {
	timeLineHeaderItem,
	timeLineTextItem,
} from './animations/anime-js.jsx';

document.addEventListener('DOMContentLoaded', function () {
	const textItem = document.querySelector('.performance__text');
	if (textItem) {
		timeLineTextItem();
	}


	//* ------------------------------- Date -------------------------------------
	let dateContainer = document.querySelector('.performance__date');
	if (!dateContainer) return;

	let now = new Date();
	let options = { month: 'short' }; // Ð¡Ð¾ÐºÑÐ°ÑÑÐ½Ð½Ð¾Ðµ Ð½Ð°Ð·Ð²Ð°Ð½Ð¸Ðµ Ð¼ÐµÑÑÑÐ°
	let day = now.getDate();
	let month = new Intl.DateTimeFormat('ru-RU', options).format(now);

	// Ð£Ð±Ð¸ÑÐ°ÐµÐ¼ ÑÐ¾ÑÐºÑ Ð¸ Ð´ÐµÐ»Ð°ÐµÐ¼ Ð¿ÐµÑÐ²ÑÑ Ð±ÑÐºÐ²Ñ Ð·Ð°Ð³Ð»Ð°Ð²Ð½Ð¾Ð¹
	month = month.replace('.', '').charAt(0).toUpperCase() + month.slice(1, -1);

	dateContainer.innerHTML = `<div class="day">${day}</div> <div class="data-wrapper">
<div class="month">${month}</div></div>`;


	//* --------------------------- Animation Header -----------------------------
	const header = document.querySelector('.header');
	const mainContent = document.querySelector('.page__main-content');
	if (header && mainContent) {
		// ÐÐ¼ÐµÐ½Ð¾Ð²Ð°Ð½Ð½Ð°Ñ ÑÑÐ½ÐºÑÐ¸Ñ Ð´Ð»Ñ Ð¾Ð±ÑÐ°Ð±Ð¾ÑÐºÐ¸ ÑÐºÑÐ¾Ð»Ð»Ð¸Ð½Ð³Ð°
		const handleScroll = () => {
			const mainContentTop = mainContent.getBoundingClientRect().top;

			if (mainContentTop < 0) {
				header.classList.add('with-border');
				header.classList.remove('without-border');
			} else {
				header.classList.add('without-border');
				header.classList.remove('with-border');
			}
		};
		// ÐÑÐ¿Ð¾Ð»Ð½ÐµÐ½Ð¸Ðµ timeLineHeaderItem Ð¿ÑÐ¸ Ð·Ð°Ð³ÑÑÐ·ÐºÐµ
		timeLineHeaderItem();

		// ÐÐ¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ðµ Ð¾Ð±ÑÐ°Ð±Ð¾ÑÑÐ¸ÐºÐ° ÑÐºÑÐ¾Ð»Ð»Ð¸Ð½Ð³Ð°
		window.addEventListener('scroll', handleScroll);

		// ÐÑÐ¸ÑÑÐºÐ° Ð¾Ð±ÑÐ°Ð±Ð¾ÑÑÐ¸ÐºÐ¾Ð² Ð¿ÑÐ¸ Ð²ÑÐ³ÑÑÐ·ÐºÐµ ÑÑÑÐ°Ð½Ð¸ÑÑ
		window.addEventListener('beforeunload', () => {
			window.removeEventListener('scroll', handleScroll);
		});
	}
});



